
.App {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  /* font-family: myFont; */
}

canvas {
  border: solid 1px black;
}

audio {
  display: none;
}

form {
  width: 20em;
}

button {
  margin-right: 10px;
}

.speedBtn, #resetBtn {
  border: solid 1px black;
  border-radius: 20px;
  margin-left: 10px;
  margin-right: 10px;
}

@font-face {
  font-family: 'harryPotterFont';
  src: local('harryPotterFont'), url("./harryPotterFont.TTF") format("truetype");
}